import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddressDataComponent } from './views/address-data/address-data.component';
import { PersonalDataComponent } from './views/personal-data/personal-data.component';

const routes: Routes = [
    { path: 'personal-data', component: PersonalDataComponent },
    { path: 'address-data', component: AddressDataComponent },
    { path: '**', component: PersonalDataComponent },
    { path: '',   redirectTo: '/personal-data', pathMatch: 'full' },
];
@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule {}
